@import "../../variables";

.works {
	font-size: 16px;
	height: 100vh;
	overflow: scroll;
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}

.works::-webkit-scrollbar {
	display: none;
}

.title {
	position: relative;
	width: 30%;
	z-index: 1;
	font-size: 4.5em;
	color: $text_color;
	font-family: "Baskerville";
	padding-top: 4rem;
	padding-left: 2.5%;
	letter-spacing: 0.3rem;
}

.blocks {
	width: 95%;
	margin: 10vh auto 40px;
}

.block {
	margin-top: 3rem;
	width: 100%;
	overflow: hidden;
	border-radius: 30px;
	height: 32rem;
	box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.25);
	display: flex;
	justify-content: space-between;
}

.first {
	background: linear-gradient(
		180deg,
		#876d9d 0%,
		rgba(135, 109, 157, 0) 100%
	);
}

.second {
	background: linear-gradient(
		360deg,
		#876d9d 0%,
		rgba(135, 109, 157, 0) 100%
	);
}

.left {
	width: 35%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.left_info {
	margin-left: 2em;
	margin-top: 2.8em;
}

.left_info_title {
	font-size: 3em;
	color: $text_color;
}

.left_info_text {
	margin-top: 1.5rem;
	font-size: 1.5em;
	line-height: 2.25rem;
	color: $text_color;
}

.left_info_text span {
	color: $main_color;
	font-weight: 600;
}

.left_link {
	font-size: 1.5em;
	background-color: $main_color;
	width: 40%;
	height: 3.5rem;
	border-radius: 0 20px 0 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: 0.2s linear;
	outline: none;
	border: none;
	cursor: pointer;
	color: $bg_color;
	display: flex;
	gap: 1rem;
}

.left_link_image {
	width: 24px;
	height: 24px;
}

.left_link:hover {
	height: 4rem;
	width: 45%;
	font-size: 1.8em;
}

.right {
	width: 65%;
	display: flex;
	justify-content: flex-end;
}

.right_block {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	gap: 0.8rem;
	margin-right: -3vh;
}

.right_column {
	width: 48%;
	display: flex;
	margin-top: -5vh;
	height: 40rem;
	flex-direction: column;
	transform: rotate(5deg);
}

.right_column_image {
	width: 100%;
	height: 50%;
	object-fit: cover;
	filter: saturate(0);
	transition: 0.2s linear;
}

.right_column:hover {
	.right_column_image {
		filter: saturate(1);
	}
}

.right_another {
	width: 65%;
	display: flex;
	justify-content: flex-end;
	gap: 0.8rem;
}

.right_another_block {
	height: 60vh;
	margin-right: -5vh;
	width: 100%;
	display: flex;
	justify-content: flex-end;
	gap: 0.8rem;
}

.right_another_image {
	rotate: (5deg);
	width: 32%;
	height: 130%;
	margin-top: -10%;
	object-fit: cover;
	filter: saturate(0);
	transition: 0.2s linear;
}

.right_another_image:hover {
	filter: saturate(1);
}

@media (max-width: 1281px) {
	.works {
		font-size: 12px;
	}

	.left_link {
		width: 60%;
	}
}


@media (max-width: 1025px) {
	.works {
		font-size: 10px;
	}

	.left_info_text {
		line-height: 2rem;
		margin-top: 1rem;
	}

	.right_another_image {
		margin-top: -5%;
	}

	.block {
		height: 25rem;
	}
}

@media (max-width: 850px) {
	.right {
		width: 40%;
	}

	.block {
		height: 30rem;
	}

	.left {
		width: 60%;
	}

	.left_info_title {
		font-size: 3.5em;
		font-weight: bold;
	}

	.left_info_text {
		font-size: 2em;
		line-height: 2em;
	}

	.right_column:first-child {
		display: none;
	}

	.right_column {
		width: 100%;
	}

	.left_link {
		width: 50%;
		font-size: 2em;
	}

	.right_another {
		width: 40%;
	}

	.right_another_image {
		display: none;
	}

	.right_another_image:first-child {
		display: block;
		width: 100%;
		height: 110%;
	}
}


@media (max-width: 700px) {
	.works {
		font-size: 9px;
	}

	.block {
		flex-direction: column;
		height: auto;

	}

	.left {
		width: 100%;
	}

	.left_info_text {
		margin-right: 2rem;
		margin-top: 0.5rem;
		line-height: 2rem;
		font-size: 1.8em;
	}

	.right {
		display: none;
	}

	.right_another_block {
		display: none;
	}

	.left_link {
		margin-top: 2rem;
		height: 3rem;
		font-size: 2em;
		width: 50%;
		gap: 0.5rem;
	}

	.left_link_image {
		width: 16px;
		height: 16px;
	}

	.left_link:hover {
		width: 50%;
		height: 3.5rem;
	}
}

